import React from 'react';
import './Notifications.scss';
import LinkBack from './../../../../components/LinkBack/LinkBack';
import { ACCOUNT_ROUTE } from './../../../../utils/consts';
import email from '../../../../img/notifications/email.svg';
import messangers from '../../../../img/notifications/messangers.svg';
import sms from '../../../../img/notifications/sms.svg';
import Notification from '../../../../components/Notification/Notification';

const Notifications = () => {
  const notifications=[
    {title: 'Email notifacations', subtitle: 'information about all transfers will be sent by email',
      icon: email},
    {title: 'SMS', subtitle: 'Сard and account name', icon: sms},
    {title: 'Messengers', subtitle: 'Сard and account name', icon: messangers} // state for check if check
  ];
  return (
    <>
      <LinkBack title='Notification setting' path={ACCOUNT_ROUTE}/>
      <div className='notifications border-gradient'>
        <h1 className='notifications__title'>Notification setting</h1>
        <div className='notifications__content'>
          <div className='notifacations__blocks'>
          {notifications.map((notification) => 
            <Notification notification={notification} key={notification.title}/>
          )}
         </div>
        </div>
      </div>
    </>
  );
};

export default Notifications;