import React, { useRef } from 'react';
import './Notification.scss';

const Notification = ({notification}) => {
  const  circleRef = useRef(null);
  const notifacationHandler = (e) => {
    if(e.target.checked) {
      circleRef?.current?.classList.remove('switch__circle-toLeft');
      circleRef?.current?.classList.add('switch__circle-toRight');
    }
    else {
      circleRef?.current?.classList.remove('switch__circle-toRight');
      circleRef?.current?.classList.add('switch__circle-toLeft');
    }
  }
  return (
    <div className='notification-block' key={notification.title}>
      <div className='notification-block__icon'>
        <img src={notification.icon} alt={`${notification.icon}`} />
      </div>
      <div className='notification-block__content'>
        <h3 className='notification-block__title'>{notification.title}</h3>
        <p className='notification-block__subtitle'>{notification.subtitle}</p>
      </div>
      <div className="switch">
        <input type="checkbox" name={notification.title} onChange={notifacationHandler}></input>
        <span ref={circleRef} className="switch__circle"></span>
      </div>
    </div>
  );
};

export default Notification;